/* 1. Main Layout ================================= */

* {
	box-sizing: border-box;
}

body {
	background: #ccc;
}

.content-box {
	background: white;
	padding: 30px;
	font-family: sans-serif;
	width: 90%;
	max-width: 600px;
	margin: 30px auto;
	border-radius: 10px;
}

.hidden {
	display: none;
}


/* 2. Content ================================= */


h1 {
	font-weight: 700;
	font-size: 22pt;
	margin: 15px 0;
}

p {
	color: #777;
	line-height: 140%;
}



.todo-list {
	list-style: decimal;
	padding: 0 30px;
	
	.todo-item {
		padding: 10px 0;

		&:after {

		}

		&:hover {
			background: pink;
		}
	}
}

























